import "./App.css";
import TextField from "@mui/material/TextField";
import { Button } from "@mui/material";
import { useFormik } from "formik";
import * as Yup from "yup";
const Form = () => {
  const formik = useFormik({
    initialValues: {
      name: "",
      lastName: "",
      email: "",
    },
    validationSchema: Yup.object({
      name: Yup.string().max(15, "must be 15 or less").required("savaldebulo"),
      lastName: Yup.string()
        .max(15, "must be 15 or less")
        .required("savaldebulo"),
      email: Yup.string().email("must be email format").required("savaldebulo"),
    }),
    onSubmit: (values) => {
      console.log(values, formik.touched);
    },
  });
  return (
    <form onSubmit={formik.handleSubmit}>
      <TextField
        error={formik.errors.name && formik.touched.name}
        id="name"
        label="Name"
        helperText={
          formik.errors?.name && formik.touched.name ? formik.errors.name : ""
        }
        variant="outlined"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.name}
      />
      <TextField
        error={formik.errors.lastName && formik.touched.lastName}
        id="lastName"
        label="LastName"
        helperText={
          formik.errors?.lastName && formik.touched.lastName
            ? formik.errors.lastName
            : ""
        }
        variant="outlined"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.lastName}
      />{" "}
      <TextField
        error={formik.errors.email && formik.touched.email}
        id="email"
        label="Email"
        helperText={
          formik.errors?.email && formik.touched.email
            ? formik.errors.email
            : ""
        }
        variant="outlined"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.email}
      />
      <Button type="submit" variant="contained">
        Submit
      </Button>
    </form>
  );
};

export default Form;
